// components
import Avatar from '@ui/Avatar';
import Spring from '@components/Spring';

// utils
import dayjs from 'dayjs';
import axios from 'axios';

//hooks 
import { useLocation } from 'react-router-dom';
import { useState, useEffect } from 'react';




const relativeTime = require('dayjs/plugin/relativeTime')
dayjs.extend(relativeTime)



const BidsHistory = ({data, active}) => {

    const currentUrl = useLocation();
    const collectionId = currentUrl.pathname.split('/')[3]
    const tokenId = currentUrl.pathname.split('/')[4]
    const [orders, setOrders] = useState([]);


    const options = {
        method: 'GET',
        headers: {accept: '*/*', 'x-api-key': '89079f7b-ce6a-5fb9-a571-4cefa93c4b01'}
      };

    useEffect(() => {
        (async () => {
        const { data } = await axios.get(`https://api-goerli.reservoir.tools/orders/asks/v4?token=${collectionId}%3A${tokenId}`, options);
        setOrders(data.orders)
        
        })()
    },[collectionId, tokenId])
    
    return (
        <div className="d-flex flex-column g-20">
            {
                orders.map((order, index) => (
                    <Spring key={index} index={index}>
                        <div className="d-flex align-items-center g-15">
                            {/* <img src={order.source.icon} /> */}
                            <Avatar src={order.source.icon} isVerified alt={order.source.domain}
                                    size="xs"/>
                            <div className="text-sm">
                                <p className="text-overflow">
                                    {
                                        active ?
                                            <span className="text-accent text-bold">{order.price.amount.native} ETH</span>
                                            :
                                            <span>bid cancelled</span>
                                    } by <span className="text-light text-bold"> {order.maker.substring(0, 8)} </span>
                                    
                                </p>
                                {/* <span className="text-xs">{dayjs(item.date).fromNow()}</span> */}
                            </div>
                        </div>
                    </Spring>
                ))
            }
        </div>
    )
}

export default BidsHistory