// styling
import styles from './style.module.scss';

// components

import StyledTabs from '@ui/StyledTabs';

import GradientBtn from '@ui/GradientBtn';
import Like from '@ui/Like';
import BidsHistory from '@components/BidsHistory';
import Sticky from 'react-stickynode';
import { BuyModal, BidModal} from '@reservoir0x/reservoir-kit-ui'


// hooks
import {useBidModalContext} from '@contexts/bidModalContext';
import {useRef, useState} from 'react';
import {useWindowSize} from 'react-use';
import { useLocation } from 'react-router-dom';

// utils
import dayjs from 'dayjs';



// data placeholder
import item from '@db/item';
import { useEffect } from 'react';
import axios from 'axios';


const Table = () => {
    const currentUrl = useLocation()
    const collectionId = currentUrl.pathname.split('/')[3]
    const tokenId = currentUrl.pathname.split('/')[4]
    const [kind, setKind] = useState([])

    useEffect(() => {
        (async () => {
            const {data} = await axios.get(`https://api-goerli.reservoir.tools/tokens/v6?tokens=${collectionId}%3A${tokenId}&includeTopBid=true`)
            
            setKind(data.tokens[0].token.kind)
        })()
       
    },[])
    return (
        <table className={styles.table}>
            <tbody>
           
            <tr>
                <td className="text-bold text-accent">Contract Address</td>
                <td className="text-overflow" onClick={() => window.open(`https://etherscan.io/address/${collectionId}`, "_blank")}>{`${collectionId.substring(0, 8)}...${collectionId.substring(collectionId.length - 4)}`}</td>
                </tr>

            <tr>
                <td className="text-bold text-accent">Chain</td>
                <td className="text-overflow">Ethereum </td>
            </tr>
            <tr>
                <td className="text-bold text-accent">Token ID</td>
                <td className="text-overflow">{tokenId}</td>
            </tr>
            <tr>
                <td className="text-bold text-accent">Token Standard </td>
                <td className="text-overflow">{kind}</td>
            </tr>
            </tbody>
        </table>
    )
}

const ItemDetails = () => {
    const {openBidModal} = useBidModalContext();
    const activeBids = item.bids.filter(item => item.active), prevBids = item.bids.filter(item => !item.active);
    const date = useRef(dayjs().add(7, 'days').toDate());
    const isSticky = useWindowSize().width >= 768;
    const currentUrl = useLocation()
    const collectionId = currentUrl.pathname.split('/')[3]
    const tokenId = currentUrl.pathname.split('/')[4]
    const [tokenimage, setTokenImage] = useState([])
    const [name, setName] = useState([])
    const [onSalePrice, setonSalePrice] = useState([])
    const [ sourceicon, setSourceIcon] = useState([])
    const [ source, setsource] = useState([])
    const [ rarityrank, setRarityRank] = useState([])
    const [ attributes, setAttributes] = useState([])
    const openState = useState(true)



    useEffect(() => {
        (async () => {
            const { data } = await axios.get(`https://api-goerli.reservoir.tools/collections/${collectionId}/attributes/explore/v4?tokenId=${tokenId}`)
            setAttributes(data.attributes)
        }) ()
    },[collectionId, tokenId])
    
    useEffect(() => {
        (async () => {
            const {data} = await axios.get(`https://api-goerli.reservoir.tools/tokens/v6?tokens=${collectionId}%3A${tokenId}&includeTopBid=true`)
            
            setTokenImage(data.tokens[0].token.image)
            setName(data.tokens[0].token.collection.name)
            setonSalePrice(data.tokens[0].market.floorAsk.price.amount.native)
            setSourceIcon(data.tokens[0].market.floorAsk.source.icon)
            setsource(data.tokens[0].market.floorAsk.source.domain)
            setRarityRank(data.tokens[0].token.rarity)
        })()
       
    },[])
   

    const renderAttributes = () => {
        const attributeBoxes = [];
     
        for (let i = 0; i < attributes.length; i++) {
          const attribute = attributes[i];
       
          attributeBoxes.push(
            <div key={attribute.id} className={`${styles.block} border-10`}>
                <div className={styles.block_details}>
                <span className="text-xs">
                <span className="text-bold">{attribute.key} </span>
                </span>
                <span className="text-sm text-bold text-light">{attribute.value}</span>
             
              </div>
            </div>
          );
        }

        return attributeBoxes;
        
  };
    

    const tabs = [
        {label: 'Active Listing', key: 'item-1', children: <BidsHistory data={activeBids} active/>},
        {label: 'History', key: 'item-2', children: <BidsHistory data={prevBids}/>},
        {label: 'Details', key: 'item-3', children: <Table/>},
    ];

    return (
        <section className={styles.details}>
            
            <div className={`${styles.details_container} container`}>
                <Sticky enabled={isSticky} top={60} bottomBoundary="#item_main">
                    <div className="media square border-10">
                        {/* <ZoomViewer originalImg={tokenimage} /> */}
                        <img src= {tokenimage} />
                    </div>
                    
                    <div className={styles.main_creator} style={{ marginTop:"12px"}}>

                    {renderAttributes()}
                  
                    </div>
                </Sticky>
                
                <div className={styles.main} id="item_main">
                    <div className={styles.main_about}>
                        <div className="d-flex flex-column g-10">
                            {/* <Countdown date={date.current}
                                       renderer={({days, hours, minutes, seconds}) => {
                                           return <span className="h6">ðŸ”¥ {days}d {hours}h {minutes}m {seconds}s</span>;
                                       }}/> */}
                                       <span className="h6"> {name} </span>
                            <h2 className={styles.title}>#{tokenId}</h2>
                            <div className={styles.bid}>
                                <div className="d-flex g-10">Price
                                   
                                </div>
                                
                                {/* <div className="d-flex g-10">
                                    TopBid <span className="text-accent text-bold">2 ETH</span>
                                </div> */}
                            </div>
                            <div className={styles.bid}><span className="text-light text-bold" style={{fontSize:"24px" , fontWeight:"700" }}>{onSalePrice} ETH  </span></div>
                            <div className={styles.bid}><img src= {sourceicon} style={{width: "26px"}}></img> <span> { source}</span></div>

                        </div>
                        <div className={styles.actions}>
                            <Like className={`${styles.btn} ${styles.like} btn btn--icon`} count={item.likes}/>
                           
                        </div>
                        
                    </div>
                    <div className={styles.buttons}>
                          
                    </div>
                  
                    
                    <div className="main_tabs">
                        <StyledTabs tabs={tabs}/>
                        <div className={styles.buttons}>
                        <BuyModal 
                            trigger={
                            <GradientBtn tag="button">Buy Now For {onSalePrice } ETH </GradientBtn>
                        }
                        referrer = "0x22c06A1639a1e674E43F2c75A65c81e76AA921f5"
                        referrerFeeBps={1}
                        collectionId = {collectionId}
                        tokenId = {tokenId}
                        onPurchaseComplete={(data) => console.log('Purchase Complete')}
                        onPurchaseError={(error, data) => console.log('Transaction Error', error, data)}
                        onClose={(data, stepData, currentStep) => console.log('Modal Closed')}
                        />
                         <BidModal
                                trigger={
                                         <button className="btn btn--outline" >Place a bid</button>
                                    }
                            collectionId={collectionId}
                            tokenId= {tokenId}
                           
                            onBidComplete={(data) => {
                                console.log('Bid Complete', data)
                              }}
                              onBidError={(error, data) => {
                                console.log('Bid Transaction Error', error, data)
                              }}
                              onClose={(data, stepData, currentStep) => {
                                console.log('BidModal Closed')
                              }}
                         />

                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default ItemDetails